<template>
  
    <b-navbar id="navbarr" type="dark" variant="dark" toggleable="md" sticky >
      <b-navbar-brand  to="/"></b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="bnav">
          <b-nav-item to="/" no-prefetch=true>{{ $t('nav.Home') }}  <ios-home-icon/></b-nav-item>
          <HomeIcon/>
          <b-nav-item to="/products">{{ $t('nav.Products') }}  <ios-jet-icon/></b-nav-item>
          <b-nav-item to="/spareParts"> {{ $t('nav.Spare Parts') }}  <ios-cog-icon/></b-nav-item>
          <b-nav-item to="/contact">{{ $t('nav.Contact') }}   <ios-contact-icon /></b-nav-item>
          <b-nav-item to="/login">{{ $t('nav.LOG IN') }}   <ios-log-in-icon /></b-nav-item>
          <b-nav-item to="/problem"> problem  <ios-log-in-icon /></b-nav-item>
          </b-navbar-nav>
          <b-navbar-nav class="ml-auto">
          <!-- Navbar dropdowns -->
            <b-nav-item-dropdown text="Lang" >
              <b-dropdown-item href="#" 
              @click.prevent="setLocale('en')"
            ref="account"
            class="flex items-center px-3 py-3 hover:bg-gray-200"
            @keydown.up.exact.prevent=""
            @keydown.tab.exact="focusNext(false)"
            @keydown.down.exact.prevent="focusNext(true)"
            @keydown.esc.exact="hideDropdown"><img src="../../assets/usa.png"></b-dropdown-item>
              <b-dropdown-item href="#"
              @click.prevent="setLocale('de')"
            class="flex items-center px-3 py-3 hover:bg-gray-200"
            @keydown.shift.tab="focusPrevious(false)"
            @keydown.up.exact.prevent="focusPrevious(true)"
            @keydown.down.exact.prevent=""
            @keydown.tab.exact="hideDropdown"
            @keydown.esc.exact="hideDropdown"><img src="../../assets/germany.png"></b-dropdown-item>
            </b-nav-item-dropdown>
            
            
        </b-navbar-nav>
      </b-collapse> 
    </b-navbar>
    
    
  
</template>

<script>


export default {
   name: "NavBar",
  data() {
    return {
      isVisible: false,
      focusedIndex: 0,
    }
  },
  methods: {
    toggleVisibility() {
      this.isVisible = !this.isVisible
    },
    hideDropdown() {
      this.isVisible = false
      this.focusedIndex = 0
    },
    startArrowKeys() {
      if (this.isVisible) {
        // this.$refs.account.focus()
        this.$refs.dropdown.children[0].children[0].focus()
      }
    },
    focusPrevious(isArrowKey) {
      this.focusedIndex = this.focusedIndex - 1
      if (isArrowKey) {
        this.focusItem()
      }
    },
    focusNext(isArrowKey) {
      this.focusedIndex = this.focusedIndex + 1
      if (isArrowKey) {
        this.focusItem()
      }
    },
    focusItem() {
      this.$refs.dropdown.children[this.focusedIndex].children[0].focus()
    },
    setLocale(locale) {
      this.$i18n.locale = locale
      this.$router.push({
        params: { lang: locale }
      })
      this.hideDropdown()
    }

  }
}
</script>

